.card {
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    margin: auto;
    text-align: center;
    font-family: arial;
    background:#fff;
    padding-bottom: 10px;
    border-radius: 10px;
    height: 146px;
    border: 3px solid black;
    width: 100%;
    height: 100%;
    max-width: 80%;
  }

  .container{
    width: 60%;
    /*border: 1px solid black;*/
    margin: 10px;
    padding: 10px;
    display: -ms-flexbox;
    display: flex;
    height: 80%;
  }

  
.card:hover {
    -webkit-box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
            box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  }
 

img{
    width: 100%;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;

  }


.class-btns, .assignment-btns{

    background: none;
    color: inherit;
    border: none;
    padding: 0px;
    font: inherit;
    cursor: pointer;
    
  }

  .class-btns:hover, .assignment-btns:hover{
      text-decoration: underline;
  }

.class-btns{

    max-height: 100%; 
    min-height: 100%;        
    width: 100%;
}

  .assignment-btns{
    color:#008ee2;
  }


.column{
        float: left;
        max-width: 20%;
        padding: 10px;
        margin: 1px;
        margin-left: 30px;
        max-width: 20%;
  }

 

@media screen and (max-width: 600px) {
    .column {
      width: 100%;
      display: block;
      margin-bottom: 20px;
    }
  }


  .assignment-table{
    font-family: arial, sans-serif;
    border-collapse: collapse;
    width: 100%;
  }

  .assignment-td,.assignment-tr{
    border: 1px solid #dddddd;
    text-align: center;
    padding: 8px;

  }


.header, h2{
      text-align: center;
  }


